
import XHeader from 'vux/src/components/x-header/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import indexHeader from './components/indexHeader'
import { setTimeout } from 'timers';
export default {
name: 'App',
components: {XHeader, Badge, indexHeader  },
data(){
return{
isHeaderShow:true,
isIndexHeader: false,
indexHeaderTitle:'',
headerTitle: '',
rightTitle:'',
isRightTitle: false,
isHiddenBox: false,//箭头class
isEstablish: false,//创建试验
isWatchManage: false,//进度
}
},
methods:{
setHeaderShow(data){
this.isIndexHeader = false;
this.isHeaderShow = data.blod;
this.headerTitle = data.title;
this.isRightTitle = data.isRightTitle ? true: false;
this.rightTitle = data.rightTitle ? data.rightTitle : '';
this.isEstablish = data.isEstablish ? true : false;
this.isWatchManage = data.isWatchManage ? true: false;
},
setIndexHeaderTitle(data){
this.isHeaderShow = false;
this.isIndexHeader = data.flag;
this.indexHeaderTitle = data.title;
},
changeRighTitle(){
this.isHiddenBox = true;
this.$refs.headerChange.changeRighTitle();
},
establishTest(){
this.$refs.headerChange.setEstablishTest();
},
scheduleManage(){
this.$refs.headerChange.scheduleManage();
},
bindBack() {
let vm = this;
let first = null;
plus.key.addEventListener("backbutton", () => {
if (
this.$route.name == "LabManagerIndex" ||
this.$route.name == "LogingIndex"||
this.$route.name == "gaugerIndex"||
this.$route.name == "EquipmentengineerIndex"
) {
if (!first) {
first = new Date().getTime(); 
plus.nativeUI.toast("再按一次退出应用");
setTimeout(function() {

first = null;
}, 2000);
} else {
if (new Date().getTime() - first < 2000) {

plus.runtime.quit(); 
}
}
}else if(this.$route.name == 'QRcodeIndex'){


this.$router.back(-1)
this.$store.commit('closeScan')
}else{
this.$router.back(-1)
}
});
},
},
mounted(){
this.bindBack()
}
}



