

import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/index.js'

Vue.config.productionTip = false


import './assets/style/resize.css';
import './assets/style/vux.scss';
import './assets/style/common.scss';



import {ProgressBar} from 'vue-ydui/dist/lib.rem/progressbar';
import {CountUp} from 'vue-ydui/dist/lib.rem/countup';
import echarts from 'echarts';
Vue.component(ProgressBar.name, ProgressBar);
Vue.component(CountUp.name, CountUp);
Vue.prototype.$echarts = echarts


import updataFile from './components/commentMethods/updataFile';
Vue.prototype.$updataFile  = updataFile;


import XButton from 'vux/src/components/x-button/index.vue'

Vue.component('x-button', XButton)
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

Vue.use(ToastPlugin, {position: 'center'})
Vue.use(LoadingPlugin)

/**@name接口请求 */
import conf from "./config/index.js";
import http from "./track/http.js";

Vue.prototype.$http = http;
Vue.prototype.$conf = conf;


import VConsole from "vconsole";
if (process.env.NODE_ENV === 'production') {
new VConsole()
}











setTimeout(()=>{
if(window.plus){
var isImmersedStatusbar = plus.navigator.isImmersedStatusbar();
if (isImmersedStatusbar) {
var StatusbarHeight = plus.navigator.getStatusbarHeight();
if (plus.device.model.toString() == 'iPhoneX') {
Vue.prototype.$StatusbarHeight = StatusbarHeight + 15;
} else {
if (plus.os.version < 11) {
FastClick.attach(document.body)
}
Vue.prototype.$StatusbarHeight = StatusbarHeight;
}
}
plus.navigator.setStatusBarStyle('dark');
plus.webview.currentWebview().setStyle({
softinputMode: "adjustResize" 
});
new Vue({
el: '#app',
router,
store,
render: h => h(App)
})
}
},1000)

